@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

/* feature list */
.feature-list__item {
	margin-bottom: $bottom-space--md;
	@include clear;

	&:last-child {
		margin-bottom: 0; } }

.feature-list__img {
	float: left;
	width: 25%;
	margin: 0.1em $bottom-space $bottom-space--sm 0;

	@include media("<mob-lg") {
		width: auto;
		float: none;
		margin-right: 0; } }

.feature-list__content {
	overflow: hidden;

	h3, p {
		margin-bottom: $bottom-space--sm;

		&:last-child {
			margin-bottom: 0; } } }

/* feature list end */
