@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

/* HOME PAGE */
.home-page .wrapper {
	padding-top: 0; }

/*scroll sections */
.main-section-js {
	position: relative;
	overflow: hidden;
	padding-top: $header-height; }

.main-section__container {
	height: 100%; }

.main-section__align {
	//max-width: 640px
	height: 100%;
	margin: 0 auto; }

/*scroll sections end */

.main-section--footer {
	height: auto !important;
	padding-top: 0;
	background-color: #fafafa; }

.main-section--categories {
	.main-section__align {
		overflow: hidden; } }

/* main section */

/* main section end */

/* fullpage navigation */
#fp-nav {
	display: table;
	position: fixed;
	top: 0;
	left: $page-layout-width;
	height: 100vh;
	width: $sidebar-width;
	margin: 0 0 0 -#{$sidebar-width}!important;
	padding-top: $header-height;
	background-color: rgba($bg-color, 0.5);
	border: solid $border-color;
	border-width: 0 0 0 1px;

	@include media('<1920px') {
		left: 100%; }

	ul {
		$dot-size: 8px;
		$dot-size-active: 20px;
		$dot-space: 40px;

		display: table-cell;
		vertical-align: middle;
		text-align: center;

		li {
			@include size(auto);
			line-height: $dot-size;
			margin: 0;
			//padding: round($dot-space/2 - ($dot-size-active - $dot-size)/2) 0
			padding: 0;

			&:last-child {
				display: none; }

			a {
				//+display-inline-block-top
				//+size($dot-size-active)
				//padding: round(($dot-size-active - $dot-size)/2)
				display: block;
				padding: round($dot-space/2);

				&::before {
					@include pseudo();
					@include size(#{$dot-size-active});
					@include position(50%);
					margin: -#{round($dot-size-active/2)} 0 0 -#{round($dot-size-active/2)};
					border: 1px solid $base-color;
					border-radius: 50%;
					opacity: 0;
					transform: scale(0, 0) translateZ(0);
					transition: {
						property: transform, opacity;
						duration: $animation-speed; } }

				span {
					@include display-inline-block-top;
					position: relative;
					@include size($dot-size);
					@include position(0);
					margin: 0;
					background-color: $text-color;
					@if $animation-use {
						transition: none; } }

				&:hover {
					span {
						background-color: $base-color; } }

				&.active {
					&::before {
						transform: scale(1, 1) translateZ(0);
						opacity: 1; }

					span {
						margin: 0;
						@include size($dot-size);
						background-color: $base-color; } } }

			&:hover {
				a {
					&, &.active {
						span {
							@include size($dot-size);
							margin: 0; } } } } } } }

/* fullpage navigation end */

/* Styled scrollbars */
.iScrollVerticalScrollbar {
	position: absolute;
	z-index: 9999;
	width: 8px;
	bottom: 2px;
	top: 2px;
	right: 2px;
	overflow: hidden; }

.iScrollVerticalScrollbar.iScrollBothScrollbars {
	bottom: 18px; }

.iScrollIndicator {
	position: absolute;
	background: $base-color;
	border-radius: 0;
	width: 100%; }

/* special styles for home page */
.home-page {
	// header
	.header__holder {
		padding-left: $layout-spaces + $logo-full-width - $logo-width; }

	// logo
	.logo {
		width: $logo-full-width; }

	// footer
	.footer__holder {
		position: relative;
		top: auto;
		right: auto;
		bottom: auto;
		left: auto;
		width: auto;
		border-top: 0; } }

/* special styles for home page end */
/* HOME PAGE end */
