@import "../utils/functions";
@import "../utils/placeholders";

// medium query
//$breakpoints: ('mob-sm': 320px, 'mob-md': 480px, 'mob-lg': 640px, 'tab-sm': 768px, 'tab-lg': 992px, 'desk-sm': 1200px, 'desk-md': 1366px, 'desk-lg': 1600px, 'desk-hd': 1920px)
$breakpoints: ('desk-hd': 1920px, 'desk-lg': 1600px, 'desk-md': 1366px, 'desk-sm': 1200px, 'tab-lg': 992px, 'tab-sm': 768px, 'mob-lg': 640px, 'mob-md': 480px, 'mob-sm': 320px);

// base
$font-size: 1.6rem !default;
$line-height: round-float(3.0rem/$font-size, 2) !default;
// ===============
$font-size--sm: 1.4rem !default;
$line-height--sm: round-float(2.4rem/$font-size--sm, 2) !default;
// ===============
$font-size--less: 1.2rem !default;
$line-height--less: round-float(2.0rem/$font-size--less, 2) !default;
// ===============
$letter-spacing: 0.025em !default;

// =======h1========
$font-size-h1: 72px !default;
$line-height-h1: round-float(72px/$font-size-h1, 2) !default;
// md ===
$font-size-h1--lg: 42px !default;
$line-height-h1--lg: round-float(42px/$font-size-h1--lg, 2) !default;
// md ===
$font-size-h1--md: 36px !default;
$line-height-h1--md: round-float(36px/$font-size-h1--md, 2) !default;
// sm ===
$font-size-h1--sm: 30px !default;
$line-height-h1--sm: round-float(30px/$font-size-h1--sm, 2) !default;

// =======h2========
$font-size-h2: round-float($font-size-h1*36/72, 1) !default;
$line-height-h2: round-float(36/36, 2) !default;
// md ===
$font-size-h2--md: round-float($font-size-h1--md*0.84, 1) !default;
//$line-height-h2--md: round-float(($line-height-h1--md*1.02),2) !default
$line-height-h2--md: 1 !default;
// sm ===
$font-size-h2--sm: round-float($font-size-h1--sm*0.87, 1) !default;
$line-height-h2--sm: 1 !default;

// =======h3========
$font-size-h3: round-float($font-size-h1*28/72, 1) !default;
$line-height-h3: round-float(28/28, 2) !default;
// md ===
$font-size-h3--md: round-float($font-size-h1--md*0.78, 1) !default;
$line-height-h3--md: 1 !default;
// sm ===
$font-size-h3--sm: round-float($font-size-h1--sm*0.73, 1) !default;
$line-height-h3--sm: 1 !default;

// =======h4========
$font-size-h4: round-float($font-size-h1*22/72, 1) !default;
$line-height-h4: round-float(22/22, 2) !default;

// =======h5========
$font-size-h5: $font-size !default;
$line-height-h5: $line-height !default;

// =======h6========
$font-size-h6: round-float($font-size*0.75, 1) !default;
$line-height-h6: round-float($line-height*1.25, 2) !default;

$bottom-space: round-float($font-size*$line-height*2/3, 1) !default;
$bottom-space--lg: round-float($bottom-space*3, 1) !default;
$bottom-space--md: round-float($bottom-space*2.0, 1) !default;
$bottom-space--ml: round-float($bottom-space*1.5, 1) !default;
$bottom-space--sm: round-float($bottom-space*0.75, 1) !default;

// =======Цветовая схема========
// цвет бренда
$base-color: #ff6501 !default;
$base-color-alt: lightskyblue !default;
// цвет фона страницы
$bg-color: white !default;
// цвет фона элементов выделяющихся на страницы
$bg-color-alt: #f2f2f2 !default;
// цвет фона выделенных элементов
$bg-color-selected: #f3f3f3 !default;
// цвет основного текста
$text-color: #1a1a1a !default;
// цвет основных заголовков
$title-color: $text-color !default;
// цвет текста светлее основного
$text-color-light: #b2b2b2 !default;
// цвет ссылки
$link-color: $base-color !default;
// цвет линий и границ
$border-color: #e6e6e6 !default;
// цвет специальных элементов
$mark-color: tomato !default;
// =======Темы и спецверсии========
// главные контрастные цвета (обычно, белый и черный)
$text-color-base: black !default; // использовать для цветовых тем и спецверсий
$bg-color-base: white !default; // использовать для цветовых тем и спецверсий

// =======Класс текстовой страницы========
// user content class
// class на контейнере, в котором будет использоваться стилизация
// типографических элементов: списков, таблиц, цытат и т.д.
$user-content: user-content !default;

// =======Layout========
// отступы слева и справа для всего сайта
$page-layout-space-horizontal: 0 !default;
// максимальная ширина сайта
$page-layout-width: 1920px !default;
// отступы между основновными элементами странницы
$layout-spaces: 100px - 9px !default; // -9px (x2) для компенсации скролла
$layout-spaces--hd: 70px - 9px !default; // -9px (x2) для компенсации скролла
$layout-spaces--lg: 36px - 9px !default; // -9px (x2) для компенсации скролла
$layout-spaces--sm: 20px !default;
// минимальные отступы слева и справа у текствого блока
$text-layout-space-horizontal: 40px !default;
$text-layout-space-horizontal--lg: 30px !default;
$text-layout-space-horizontal--md: 20px !default;
$text-layout-space-horizontal--sm: 15px !default;
// максимальная ширина текстового блока
$text-layout-width: 640px !default;
// максимальная ширина текстового блока выходящего за пределы текста
$flood-layout-width: 980px !default;
// отступы внутри блочных элементов
$grid-space: 30px !default;
$grid-space--lg: 20px !default;
$grid-space--sm: 10px !default;

// header
$header-height: 100px !default;
$header-height--lg: 80px !default;

// sidebar
$sidebar-width: 100px !default;
$sidebar-width--lg: 80px !default;
$sidebar-inside-space: 40px !default;
$sidebar-inside-space--lg: 30px !default;

// footer
$footer-grid-space: 100px !default;
$footer-grid-space--lg: 40px !default;

// logo
$logo-width: $sidebar-width !default;
$logo-full-width: 280px !default;

// navigation
//$nav-item-space: 20px !default
//$nav-item-space--sm: 15px !default

// sliders
$slider-arrow-size: 50px !default;

// article
$article-footer-height: 126px !default;

// shutter
$shutter-width: 975px !default;
$shutter-aside-width: 326px !default;
$shutter-space: 100px, 100px !default;

// main slider
$main-slider-color: $bg-color-base !default;

// tape slider
$tape-slider-color: $text-color !default;

// news preview
$news-preview-img-size: (250px, 250px) !default; // width, height
$news-preview-space-between: 60px !default;
$news-preview-footer-height: 30px !default;

// previews tabs
$previews-tabs-top-space: 230px !default;

// =======Form========
$form-text-color: $text-color !default;
$form-placeholder-color: $text-color-light !default;
$form-bg-color: white !default;
$form-border-color: $text-color !default;
$form-border-focus-color: $base-color !default;
$form-error-color: #ff203b !default;
$form-success-color: #319c46 !default;
$form-bottom-space: 3rem !default;
$form-bottom-space--sm: 1.5em !default;
$form-gap: 40px !default;
// ========checkbox&radio==========
$checkbox-color: $text-color;
// =========form font=========
$form-font-size: 16px !default;
$form-line-height: 24px !default;
$form-input-height: 50px !default;
$form-letter-spacing: null !default;
// =========form layout=========
$form-border-width: 0 !default;
$form-input-space-vt: floor(($form-input-height - $form-line-height - $form-border-width*2)/2) !default; // верхний паддинг округляем в нижнюю сторону
$form-input-space-vb: ceil(($form-input-height - $form-line-height - $form-border-width*2)/2) !default; // нижний паддинг округляем в верхнюю сторону
$form-input-space-h: 30px !default;
// =========select=========
$select-space-v: 12px !default;
$select-space-h: $form-input-space-h !default;
$select-arrow-width: 40px !default;
$select-arrow-height: 100% !default;
$multiple-item-space: 3px;

// =========Buttons=========
$btn-bg-color: $text-color !default;
$btn-bg-hover-color: $base-color !default;
$btn-text-color: white !default;
$btn-disabled-color: #ccc !default;

// =========Animation=========
$animation-use: false !default;
$animation-speed: 0.2s !default;
$animation-speed-mouseleave: 0.35s !default;

$animation-speed-section: 1s !default;
$animation-speed-element: 1s !default;
$animation-speed-element-delay: 0.8s !default;

// =========special style=========

// =========fonts style=========
// основоной шрифт
$font-norm: 'gothampro-regular', sans-serif !default;
$font-bold: 'gothampro-bold', sans-serif !default;
$font-italic: 'gothampro-italic', sans-serif !default;
$font-bold-italic: 'gothampro-bold-italic', sans-serif !default;
$font-medium: 'gothampro-madium', sans-serif !default;
// дополнительный шрифт
$font-alt: 'minionpro-regular', serif !default;
$font-alt-bold: 'minionpro-bold', serif !default;
$font-alt-italic: 'minionpro-italic', serif !default;
$font-alt-medium: 'minionpro-medium', serif !default;

// ============= обнуление жирности и наклонности шрифта ==============
// ============= использовать, если подключается font-face ==============
@mixin font-reset() {
	font: {
		weight: normal;
		style: normal; } }

// ============= основоной шрифт ==============
@mixin font-normal() {
	font: {
		family: $font-norm; }
	@include font-reset; }

@mixin font-bold() {
	font: {
		family: $font-bold; }
	@include font-reset; }

@mixin font-italic() {
	font: {
		family: $font-italic; }
	@include font-reset; }

@mixin font-bold-italic() {
	font: {
		family: $font-bold-italic; }
	@include font-reset; }

@mixin font-medium() {
	font: {
		family: $font-medium; }
	@include font-reset; }

// ========== дополнительный шрифт ============
@mixin font-alt() {
	font: {
		family: $font-alt; }
	@include font-reset; }

@mixin font-alt-bold() {
	font: {
		family: $font-alt-bold; }
	@include font-reset; }

@mixin font-alt-italic() {
	font: {
		family: $font-alt-italic; }
	@include font-reset; }

@mixin font-alt-medium() {
	font: {
		family: $font-alt-medium; }
	@include font-reset; }
