@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

/* FOOTER */
.footer {
	.layout {
		position: relative; }

	.copyright {
		display: block;
		@include font-alt-italic;
		@include font(18px, round-float(22/18, 2));
		letter-spacing: $letter-spacing;
		color: $text-color-light; } }

.footer__holder {
	overflow: hidden;
	position: absolute;
	top: auto;
	right: auto;
	bottom: 0;
	cursor: default;
	border-top: 1px solid $border-color; }

/* footer common / footer bottom */
.footer-common-holder, .footer-bottom__holder {
	@include push-auto;
	width: 1380px;
	max-width: 100%; }

.footer-common__cols, .footer-bottom__cols {
	@include grid-parent($footer-grid-space);
	@include clear-spacing;

	@include media('<desk-lg') {
		@include grid-parent($footer-grid-space--lg); } }

.footer-common__col, .footer-bottom__col {
	position: relative;
	@include grid-item(3, $footer-grid-space);
	@include font($font-size, $line-height);
	letter-spacing: $letter-spacing;
	text-align: center;

	@include media('<desk-lg') {
		@include grid-item-responsive(2, $footer-grid-space--lg); }

	@include media('<mob-lg') {
		@include grid-item-responsive(1, $footer-grid-space--lg); } }

/* footer common / footer bottom end */

/* footer common */
.footer-common {
	position: relative;
	color: inherit;

	a {
		color: inherit;
		text-decoration: underline;

		@if $animation-use {
			transition: {
				property: color;
				duration: $animation-speed; } } }

	.dl {
		list-style: none;

		li {
			margin-bottom: 0; }

		.dt {
			color: $text-color-light; }

		.dt, .dd {
			@include display-inline-block-top; }

		.hide {
			display: none; } }

	.adr {
		@include font-normal; } }

.footer-common__title {
	display: block;
	text-align: center;
	@include font-d;
	margin-bottom: $bottom-space--sm; }

.footer-common-list {
	list-style: none;
	padding: 0;
	margin: 0;

	li {
		margin-bottom: $bottom-space--sm;
		overflow: hidden;

		&:last-child {
			margin-bottom: 0; } } }

.footer-common__cell {
	margin-bottom: $bottom-space--sm;

	&:last-child {
		margin-bottom: 0; } }

/* footer common end */

/* footer bottom */
.footer-bottom {
	color: $text-color;
	border-top: 1px solid $border-color; }

.footer-bottom__holder {
	position: relative;
	text-align: justify;
	overflow: hidden;
	cursor: default;

	@include clear-spacing;

	&::after {
		content: "1";
		display: inline-block;
		margin-left: 100%;
		height: 1px;
		overflow: hidden; } }

/* footer bottom end */

/* developer */
.developer {
	display: inline-block;

	a {
		text-decoration: none;

		.no-touchevents &:hover {
			text-decoration: none; } } }

.developer__label {
	display: block;
	text-align: center;
	@include font-alt-italic;
	@include font(18px, round-float(22/18, 2));
	letter-spacing: $letter-spacing;
	color: #ccc;
	margin-bottom: 5px; }

.developer__logo {
	display: block;
	@include size(154px, 20px);
	position: relative;
	padding-right: 3px;
	text-align: left;
	text-indent: -9999px;

	&::before, &::after {
		@include pseudo();
		@include position(0, 0);
		@include size(149px, 16px);

		@if $animation-use {
			transition: {
				property: opacity, transform, filter;
				duration: 0.2s; } } }

	&::before {
		@include pseudo();
		@include position(4px, 4px);
		background: url('data:image/png;base64,');
		background-size: 2px;

		//.no-touchevents a:hover
		//ransform: translate(0, 1px)
		//ilter: blur(2px)
 }		//pacity: 0.6

	&::after {
		@include pseudo();
		background: #00ef85 url('data:image/png;base64,') no-repeat center center;
		background-size: auto 16px;

		.no-touchevents a:hover & {
			transform: translate(0, -1px); } } }

/* developer end */
/* FOOTER end */
