@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

/* user options */
$uo-space: 34px;

.user-options {
	position: relative;
	list-style: none;
	padding: 0;
	margin: 0 -#{round($uo-space/2)};
	@include clear-spacing;
	line-height: 1; }

.user-options__item {
	position: relative;
	@include display-inline-block-middle;
	@include padding-horizontal(round($uo-space/2));
	@include font($font-size, 1);
	letter-spacing: normal;

	& > a {
		display: block;
		position: relative;
		color: $text-color;
		// для svg
		fill: $text-color;
		text-decoration: none;
		text-align: center;

		@if $animation-use {
			transition: {
				property: color;
				duration: $animation-speed; } }

		& > * {
			@include display-inline-block-middle; }

		&::before {
			@include pseudo-inline-block();
			vertical-align: middle;
			height: 100%; }

		svg {
			@include size(18px);
			@include display-inline-block-middle;

			& ~ span {
				padding-left: 10px; } }

		path {
			fill: inherit;

			@if $animation-use {
				transition: {
					property: fill;
					duration: $animation-speed; } } }

		& > span {
			@include display-inline-block-middle; }

		// hover
		&:hover {
			color: $link-color;
			fill: $link-color; } } }

.basket-link-counter {
	$size: 18px;
	$border: 2px;
	position: absolute;
	left: 100%;
	bottom: 100%;
	margin: 0 0 -14px -9px;
	padding: 0 2px;
	height: $size+$border*2;
	min-width: $size+$border*2;
	border-radius: round(($size+$border*2)/2);
	background-color: $base-color;
	border: $border solid $bg-color-base;
	@include font-medium;
	@include font(11px, $size);
	color: $bg-color-base;
	text-align: center; }

