/*======================================================*/
/* VENDOR */
/*======================================================*/
/* jFiler */
.upload-file {
	opacity: 0;
}

.jFiler {
	position: relative;
}

.jFiler-input-dragDrop {
	margin: 0;
	cursor: pointer;
	padding: 20px;
	background: white;
	border: 1px dashed #969696;
	transition: none;
}

.no-touchevents .jFiler-input-dragDrop:hover {
	border-color: #ff6501;
}

.jFiler-input-dragDrop.dragged .jFiler-input-icon i {
	transform: translate(0, -8px);
}

.jFiler-input-dragDrop .jFiler-input-icon {
	margin-top: 0;
	margin-bottom: 15px;
	text-align: center;
}

.jFiler-input-dragDrop .jFiler-input-icon i {
	display: inline-block;
	vertical-align: top;
	background: url(../img/attach.png) no-repeat center;
	background-size: contain;
	width: 40px;
	height: 32px;
	transition-property: background-color, border-color, transform;
	transition-duration: 0.2s;
}

.jFiler-input-dragDrop .jFiler-input-text strong {
	display: block;
	font-family: "gothampro-regular", sans-serif;
	font-weight: normal;
	font-style: normal;
	font-size: 1.6rem;
	line-height: 1.25;
	color: #1a1a1a;
	text-align: center;
}

.jFiler-items {
	margin: 50px 0 0;
}

.jFiler-items-list, .user-content .jFiler-items-list {
	margin: 1.5rem 0;
}

.jFiler-items-list li, .user-content .jFiler-items-list li {
	padding-left: 0;
}

.jFiler-items-list li::before, .user-content .jFiler-items-list li::before {
	content: none;
}

.jFiler-item {
	margin: 1.5rem 0;
	font-size: 1.4rem;
	line-height: 1.71;
	color: #1a1a1a;
	overflow: hidden;
}

.jFiler-item-icon {
	display: none;
}

.jFiler-item-title {
	display: inline-block;
	vertical-align: top;
	font-family: "gothampro-bold", sans-serif;
	font-weight: normal;
	font-style: normal;
	margin-right: 10px;
}

.jFiler-item-others {
	display: inline-block;
	vertical-align: top;
	color: #b2b2b2;
	position: relative;
}

.jFiler-item-others::after {
	content: '';
	position: absolute;
	left: 100%;
	bottom: 0;
	width: 999px;
	height: 0;
	border-bottom: 1px dotted #e6e6e6;
	margin: 0 0 3px;
}

.jFiler-item-others span {
	padding-right: 5px;
}

.jFiler-item-assets {
	position: absolute;
	right: 0;
	bottom: 0;
}

.jFiler-item-assets ul,
.user-content ul .jFiler-item-assets ul {
	margin: 0;
}

.jFiler-item-assets ul li,
.user-content ul .jFiler-item-assets ul li {
	margin: 0;
	padding: 0 0 3px 8px !important;
	background: #f2f2f2;
}

.jFiler-item-trash-action {
	display: block;
	position: relative;
	width: 20px;
	height: 20px;
	z-index: 2;
	cursor: pointer;
	background-color: transparent;
}

.jFiler-item-trash-action::before, .jFiler-item-trash-action::after {
	content: '';
	position: absolute;
	left: 50%;
	top: 50%;
	width: 16px;
	height: 2px;
	border-radius: 2px;
	background: #1a1a1a;
	margin: -1px 0 0 -8px;
	transition: none;
}

.jFiler-item-trash-action::before {
	transform: rotate(-45deg);
}

.jFiler-item-trash-action::after {
	transform: rotate(45deg);
}

.no-touchevents .jFiler-item-trash-action:hover::before,
.no-touchevents .jFiler-item-trash-action:hover::after {
	background-color: #ff6501;
}

/* jFiler end */
/* slick sliders */
.slick-arrow {
	width: 50px;
	height: 50px;
	display: block;
	position: absolute;
	border: none;
	border-radius: 0;
	background: none;
	outline: none;
	box-shadow: none;
	opacity: 1;
	z-index: 6;
	margin-right: -5px;
	margin-left: -5px;
	transition: none;
}

.slick-arrow::before {
	content: none;
}

.slick-arrow::after {
	top: 50%;
	margin-top: -3px;
	content: "";
}

.no-touchevents .slick-arrow:hover {
	opacity: 1;
}

.no-touchevents .slick-arrow:hover.slick-prev::after {
	border-right-color: #ff6501;
}

.no-touchevents .slick-arrow:hover.slick-next::after {
	border-left-color: #ff6501;
}

.slick-arrow.slick-disabled, .no-touchevents .slick-arrow.slick-disabled {
	opacity: 0.25;
	-ms-filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=25);
	filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=25);
	cursor: default;
}

.slick-arrow.slick-disabled::before, .no-touchevents .slick-arrow.slick-disabled::before {
	opacity: 1;
}

.slick-prev {
	left: 0;
}

.slick-prev::after {
	width: 0;
	height: 0;
	z-index: 2;
	border-right: 9px solid #1a1a1a;
	border-bottom: 6px solid transparent;
	border-top: 6px solid transparent;
	left: 50%;
}

.slick-next {
	right: 0;
}

.slick-next::after {
	width: 0;
	height: 0;
	z-index: 2;
	border-left: 9px solid #1a1a1a;
	border-bottom: 6px solid transparent;
	border-top: 6px solid transparent;
	right: 50%;
}

.slick-dots {
	margin: 0;
	line-height: 0;
}

.user-content .slick-dots {
	margin: 0;
	max-width: none;
}

.slick-dots li {
	vertical-align: top;
	width: auto;
	height: auto;
	margin: 0;
}

.slick-dots li, .user-content .slick-dots li {
	padding: 4px 10px;
	margin: 0;
}

.slick-dots li::before, .user-content .slick-dots li::before {
	content: none;
}

.slick-dots li button {
	width: 12px;
	height: 12px;
	padding: 0;
	border-radius: 50%;
	background: #ff6501;
	position: relative;
	transition: none;
}

.slick-dots li button::before {
	content: '';
	position: absolute;
	left: 2px;
	top: 2px;
	height: 8px;
	width: 8px;
	background: #fff;
	border-radius: 50%;
	opacity: 1;
	box-shadow: 0 2px 3px rgba(0, 0, 0, 0.2);
	transition: none;
}

.slick-dots li.slick-active button::before {
	opacity: 0;
}

/* slick sliders end */
/* swiper slider */
.swiper-button-prev,
.swiper-button-next {
	overflow: hidden;
	text-indent: -9999px;
	width: 52px;
	height: 22px;
	margin-top: -11px;
	background: none;
}

.swiper-button-prev::before, .swiper-button-prev::after,
.swiper-button-next::before,
.swiper-button-next::after {
	content: "";
	display: block;
	position: absolute;
	border: 2px solid #1a1a1a;
	transform: none;
}

.swiper-button-prev::before,
.swiper-button-next::before {
	top: 50%;
	left: 50%;
	width: 51px;
	height: 0;
	border-left: none;
	border-right: none;
	border-bottom: none;
	margin: -1px 0 0 -26px;
}

.swiper-button-prev::after,
.swiper-button-next::after {
	width: 13px;
	height: 13px;
	border-bottom: none;
	top: 50%;
}

.swiper-button-prev:not(.swiper-button-disabled):hover::before, .swiper-button-prev:not(.swiper-button-disabled):hover::after,
.swiper-button-next:not(.swiper-button-disabled):hover::before,
.swiper-button-next:not(.swiper-button-disabled):hover::after {
	border-color: #ff6501;
}

.swiper-button-prev.swiper-button-disabled,
.swiper-button-next.swiper-button-disabled {
	pointer-events: auto;
}

.swiper-button-prev::after {
	left: 0;
	border-right: none;
	transform-origin: left top;
	transform: rotate(-45deg);
}

.swiper-button-next::after {
	right: 0;
	border-left: none;
	transform: rotate(45deg);
	transform-origin: right top;
}

.swiper-button-prev.swiper-button-disabled {
	pointer-events: auto;
	cursor: default;
}

/* swiper slider end */
