/* btn-with-arrow */
.btn-with-arrow > i, .btn-with-arrow > span {
	display: inline-block;
	vertical-align: middle;
}

.btn-with-arrow > i {
	position: relative;
	margin-right: 10px;
	font-size: 0;
	line-height: 0;
	width: 26px;
	height: 18px;
}

.btn-with-arrow > i::before, .btn-with-arrow > i::after {
	content: "";
	display: block;
	position: absolute;
	top: 50%;
	left: 50%;
	border: 0 solid white;
	transform: none;
}

.btn-with-arrow > i::before {
	width: 26px;
	height: 2px;
	border-top-width: 2px;
	margin: -1px 0 0 -13px;
}

.btn-with-arrow > i::after {
	top: 0;
	width: 12px;
	height: 12px;
	border-width: 2px 0 0 2px;
	transform: rotate(-45deg);
	margin: 3px 0 0 -13px;
}

.btn-with-arrow > span {
	text-align: left;
	white-space: normal;
	max-width: calc(100% - 26px - 10px);
}

.btn-with-arrow > span ~ i {
	margin-right: 0;
	margin-left: 10px;
}

.btn-with-arrow > span ~ i::after {
	border-width: 0 2px 2px 0;
	margin: 3px 0 0 1px;
}

.btn-with-arrow.btn-default-alt > i::before, .btn-with-arrow.btn-default-alt > i::after {
