		height: 80px;
	}
}

.btn-menu > span {
	border: 0;
	clip: rect(0 0 0 0);
	height: 1px;
	margin: -1px;
	overflow: hidden;
	padding: 0;
	position: absolute;
	width: 1px;
	white-space: normal;
}

.btn-menu > span:active, .btn-menu > span:focus {
	clip: auto;
	height: auto;
	margin: 0;
	overflow: visible;
	position: static;
	width: auto;
}

.btn-menu::before, .btn-menu::after {
	content: "";
	display: block;
	position: absolute;
	width: 26px;
	height: 3px;
	background-color: #1a1a1a;
	top: 50%;
	left: 50%;
	margin-top: -2px;
	margin-left: -13px;
	will-change: opacity, transform;
	opacity: 0;
	transition-property: transform, opacity;
	transition-duration: 0.2s;
	transition-delay: 0s;
	transition-timing-function: cubic-bezier(0.65, 0.05, 0.36, 1);
}

.btn-menu > i {
