	clear: both;
}

@media (max-width: 479px) {
	.subscription-form .input-holder {
		height: auto;
	}
}

.subscription-form.success-form .input-holder {
	opacity: 0;
	visibility: hidden;
	transform: scale(0.76, 0.76) translateZ(0);
}

.subscription-form input[type='submit'], .subscription-form button {
	width: 50px;
	height: 50px;
	position: absolute;
	top: 0;
	right: 0;
	cursor: pointer;
	padding: 0;
}

@media (max-width: 479px) {
	.subscription-form input[type='submit'], .subscription-form button {
		float: none;
		display: block;
		width: 100%;
		height: 50px;
	}
}

.success-text {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	opacity: 0;
	visibility: hidden;
	background-color: white;
	transform: scale(0.76, 0.76) translateZ(0);
	transition-property: opacity, visibility, transform;
	transition-duration: 0.25s;
}

.subscription-form.success-form .success-text {
	opacity: 1;
	visibility: visible;
	transform: scale(1, 1) translateZ(0);
}

.success-text span {
	position: absolute;
	top: 50%;
	left: 50%;
	width: 24px;
	height: 16px;
	margin: -8px 0 0 -12px;
}

.success-text span::before, .success-text span::after {
	content: "";
	display: block;
	position: absolute;
	width: 0;
	height: 3px;
	top: 50%;
	left: 50%;
	border-radius: 2px;
	background-color: #319c46;
	backface-visibility: hidden;
	transition-property: width;
	transition-duration: 0.12s;
	transition-timing-function: linear;
}

.success-text span::before {
	margin: -7px 0 0 -19px;
	transform: rotate(45deg) translateZ(0);
	transform-origin: 0 13px;
	transition-delay: 0.12s;
}

.success-text span::after {
	transform: rotate(-50deg) translateZ(0);
	transform-origin: 0 21px;
	margin: -1px 0 0 12px;
}

.subscription-form.success-form .success-text span::before {
	width: 13px;
	transition-delay: 0s;
	transition-timing-function: cubic-bezier(0.6, -0.28, 0.74, 0.05);
}

.subscription-form.success-form .success-text span::after {
	width: 21px;
	transition-delay: 0.12s;
}

.error-text {
	position: absolute;
	top: 100%;
	left: 0;
	width: 100%;
	height: 24px;
	padding: 0 8px;
	font-size: 12px;
	line-height: 24px;
	color: #ff203b;
	max-width: 100%;
	white-space: nowrap;
	text-overflow: ellipsis;
	overflow: hidden;
	opacity: 0;
	visibility: hidden;
	transform: scale(0.76, 0.76);
	transition-property: opacity, visibility, transform;
	transition-duration: 0.1s;
}

.subscription-form.error-form .error-text {
	opacity: 1;
	visibility: visible;
	transform: scale(1, 1);
}

