/* HOME PAGE */
.home-page .wrapper {
	padding-top: 0;
}

/*scroll sections */
.main-section-js {
	position: relative;
	overflow: hidden;
	padding-top: 100px;
}

.main-section__container {
	height: 100%;
}

.main-section__align {
	height: 100%;
	margin: 0 auto;
}

/*scroll sections end */
.main-section--footer {
	height: auto !important;
