/* main section */
/* main section end */
/* fullpage navigation */
#fp-nav {
	display: table;
	position: fixed;
	top: 0;
	left: 1920px;
	height: 100vh;
	width: 100px;
	margin: 0 0 0 -100px !important;
	padding-top: 100px;
	background-color: rgba(255, 255, 255, 0.5);
	border: solid #e6e6e6;
	border-width: 0 0 0 1px;
}

@media (max-width: 1919px) {
	#fp-nav {
		left: 100%;
	}
}

#fp-nav ul {
	display: table-cell;
	vertical-align: middle;
	text-align: center;
}

#fp-nav ul li {
	width: auto;
	height: auto;
	line-height: 8px;
	margin: 0;
	padding: 0;
}

#fp-nav ul li:last-child {
	display: none;
}

#fp-nav ul li a {
	display: block;
	padding: 20px;
}

#fp-nav ul li a::before {
	content: "";
	display: block;
	position: absolute;
	width: 20px;
	height: 20px;
	top: 50%;
	left: 50%;
	margin: -10px 0 0 -10px;
	border: 1px solid #ff6501;
	border-radius: 50%;
	opacity: 0;
	transform: scale(0, 0) translateZ(0);
	transition-property: transform, opacity;
	transition-duration: 0.2s;
}

#fp-nav ul li a span {
	display: inline-block;
	vertical-align: top;
	position: relative;
	width: 8px;
	height: 8px;
	top: 0;
	left: 0;
	margin: 0;
	background-color: #1a1a1a;
}

#fp-nav ul li a:hover span {
	background-color: #ff6501;
}

#fp-nav ul li a.active::before {
	transform: scale(1, 1) translateZ(0);
	opacity: 1;
}

#fp-nav ul li a.active span {
	margin: 0;
	width: 8px;
	height: 8px;
	background-color: #ff6501;
}

#fp-nav ul li:hover a span, #fp-nav ul li:hover a.active span {
	width: 8px;
	height: 8px;
	margin: 0;
}

/* fullpage navigation end */
/* Styled scrollbars */
.iScrollVerticalScrollbar {
	position: absolute;
	z-index: 9999;
	width: 8px;
	bottom: 2px;
	top: 2px;
	right: 2px;
	overflow: hidden;
}

.iScrollVerticalScrollbar.iScrollBothScrollbars {
	bottom: 18px;
}

.iScrollIndicator {
	position: absolute;
	background: #ff6501;
	border-radius: 0;
	width: 100%;
}

