.home-page .header__holder {
	padding-left: 271px;
}

.home-page .logo {
	width: 280px;
}

.home-page .footer__holder {
	position: relative;
	top: auto;
	right: auto;
	bottom: auto;
	left: auto;
	width: auto;
	border-top: 0;
}

