.shutter {
	overflow: hidden; // необходим для скрытия скролла
	position: fixed;
	left: #{$sidebar-width};
	bottom: 0;
	@include size(100%, calc(100vh - #{$header-height}));
	min-width: 320px;
	max-width: $shutter-width;
	//padding: 0 0 120px
	background: $bg-color;
	opacity: 0;
	visibility: hidden;
	z-index: 1000;

	@include media('<tab-lg') {
		left: 0;
		//top: #{$top-panel-height--md}
		//height: calc(100vh - #{$top-panel-height--md})
		max-width: none;
		padding-top: 0; } }

.shutter__align {
	width: calc(100% + 100px);
	overflow-x: hidden;
	overflow-y: auto;

	@include media('<tab-lg') {
		width: 100%; } }

.shutter__holder {
	width: #{$shutter-width};

	@include media('<tab-lg') {
		width: 100%; } }

.shutter__row {
	&.v-middle--top, &.v-middle--bottom {
		height: 0;

		.shutter__cell {
			padding: 0; } } }

.shutter__cell {
	padding: nth($shutter-space, 1) nth($shutter-space, 2);
	vertical-align: top; }

/* shutter header */
.shutter-header {
	position: relative;
	border: 0 solid darken($bg-color-alt, 20%);

	@include media('<tab-lg') {
		text-align: center;
		padding: 15px #{$text-layout-space-horizontal--md + $header-height--lg} 15px #{$text-layout-space-horizontal--md};
		border-bottom-width: 1px; } }

/* shutter header end */

/* shutter content */
.shutter-content {
	float: left;
	width: calc(100% - #{$shutter-aside-width}); }

/* shutter content end */

/* shutter aside */
.shutter-aside {
	float: right;
	width: $shutter-aside-width; }

/* shutter aside end */

/* shutter footer */
.shutter-footer {
	padding: 0 #{$text-layout-space-horizontal--lg} 30px;
	@include font(1.2rem, 1.6);
	color: $text-color;

	@include media('<tab-lg') {
		@include padding-horizontal($text-layout-space-horizontal--md);
		text-align: center; }

	@include media('<tab-sm') {
		@include padding-horizontal($text-layout-space-horizontal--sm); }

	.copyright {
		margin-bottom: 1em;
		padding-bottom: 1em;
		@include font(1.4rem, 1.5);
		text-transform: uppercase;
		border-bottom: 1px solid $border-color;

		&:last-child {
			margin-bottom: 0;
			padding-bottom: 0;
			border: none; } }

	.map-site {
		color: $text-color-light;

		a {
			color: inherit;
			text-decoration: none;
			transition-property: color;
			transition-duration: $animation-speed;

			.no-touchevents &:hover {
				color: $base-color; } } } }

/* shutter footer end */

/* button shutter close */
.shutter {
	.btn-popup-close {
		display: none;
		position: absolute;
		right: 0;
		top: 0;

		@include media('<tab-lg') {
			display: block; } } }

/* button shutter close end */

/* shutter wide */
.shutter-wide {
	max-width: calc(100% - #{$sidebar-width});

	@include media('<tab-lg') {
		max-width: 100%; }

	.shutter__align {
		width: 100%; }

	.shutter__holder {
		width: 100%; } }

/* shutter wide end */
/* shutter end */
