@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

body {
	height: 100%;
	min-width: 320px;
	position: relative; }

.wrapper {
	margin: 0 auto;
	min-height: 100vh;
	overflow: hidden;
	position: relative;
	padding-top: $header-height;

	&::before {
		@include pseudo();
		top: 0;
		left: $page-layout-width;
		height: 100%;
		border-right: 1px solid $border-color;

		@include media('<1920px') {
			left: 100%; } } }

.main {
	position: relative;
	width: 100%;
	float: left;
	clear: both;
	z-index: 4; }

.main__holder {
	margin: 0 0 0 #{$sidebar-width};
	min-height: 50px;
	@include clear;

	.home-page & {
		margin: 0 #{$sidebar-width}; } }

.content-wrap {
	float: left;
	width: 100%; }

.content {
	padding: #{floor($layout-spaces*0.9)} #{$layout-spaces};

	@include media('<desk-lg') {
		padding: #{floor($layout-spaces--lg*0.9)} #{$layout-spaces--lg}; } }

// актуально только для текущего проекта
.layout-full-width, .layout-wide {
	@include margin-horizontal(-#{$layout-spaces});

	@include media('<desk-lg'); }

.layout-top-fit {
	margin-top: -#{floor($layout-spaces*0.9)};

	@include media('<desk-lg') {
		margin-top: -#{floor($layout-spaces--lg*0.9)}; } }

.layout-bottom-fit {
	margin-bottom: -#{floor($layout-spaces*0.9)};

	@include media('<desk-lg') {
		margin-bottom: -#{floor($layout-spaces--lg*0.9)}; } }

.entry-img {
	padding: #{floor($layout-spaces*0.9)} #{$layout-spaces};

	@include media('<desk-lg') {
		padding: #{floor($layout-spaces--lg*0.9)} #{$layout-spaces--lg}; } }

.header {
	position: fixed;
	top: 0;
	z-index: 1020;
	height: $header-height;
	width: 100%;
	@if $page-layout-width {
		max-width: #{$page-layout-width}; }
	@else {
		max-width: 100%; } }

.header__holder {
	@include padding-horizontal($layout-spaces);
	margin-left: $logo-width; }

.sidebar {
	position: fixed;
	@include position(#{$header-height}, 0);
	@include size($sidebar-width, calc(100% - #{$header-height}));
	z-index: 1001; }

.sidebar__align {
	width: #{$sidebar-width + 100px};
	background-color: inherit;

	.touchevents & {
		width: 100%; } }

.sidebar__holder {
	position: relative;
	width: $sidebar-width; }

.footer {
	position: relative;
	clear: both;
	z-index: 5; }

.footer__holder {
	$base-width: 100% - #{$sidebar-width};
	$base-width--lg: 100% - #{$sidebar-width--lg};

	& {
		left: $sidebar-width;
		width: calc(#{$base-width});
		@include padding-horizontal($layout-spaces);

		@include media('<desk-lg') {
			@include padding-horizontal($layout-spaces--lg); } } }

.footer-common, .footer-bottom {
	@include padding-vertical(round($layout-spaces/2)); }

.max-wrap {
	@if $page-layout-width {
		max-width: $page-layout-width + $page-layout-space-horizontal*2; } }

.layout {
	//+push-auto
	@if $page-layout-space-horizontal != 0 {
		@include padding-horizontal($page-layout-space-horizontal); }

	//+media('<tab-lg')
	//main &
 }	//@include padding-horizontal(0)

// максимальная ширина текстовых элементов на внутренней (текстовой) странице
.max-wrap-text {
	max-width: #{$text-layout-width + $text-layout-space-horizontal*2};
	//max-width: #{$text-layout-width + $text-layout-space-horizontal*2 + $text-layout-indent}

	@include media('<desk-lg') {
		max-width: #{$text-layout-width + $text-layout-space-horizontal--lg*2}; }

	//+media('<tab-lg')
	///max-width: #{$text-layout-width + $text-layout-space-horizontal--md*2}
	//ax-width: #{$text-layout-width + $text-layout-indent}

	@include media('<mob-lg') {
		max-width: #{$text-layout-width + $text-layout-space-horizontal--sm*2};
 } }		//max-width: #{$text-layout-width + $text-layout-indent}

// выравнивание текстовых элементов на внутренней (текстовой) странице
// и их максимальная ширина
.layout-text {
	//max-width: #{$text-layout-width + $text-layout-space-horizontal*2 + $text-layout-indent}
	//+padding-horizontal(#{$text-layout-indent + $text-layout-space-horizontal}, $text-layout-space-horizontal)
	max-width: #{$text-layout-width + $text-layout-space-horizontal*2};
	@include padding-horizontal($text-layout-space-horizontal);
	@include push-auto;

	@include media('<desk-lg') {
		max-width: #{$text-layout-width + $text-layout-space-horizontal--lg*2};
		@include padding-horizontal($text-layout-space-horizontal--lg); }

	//+media('<tab-lg')
	//ax-width: #{$text-layout-width + $text-layout-space-horizontal--md*2}
	//include padding-horizontal($text-layout-space-horizontal--md)

	@include media('<mob-lg') {
		max-width: #{$text-layout-width + $text-layout-space-horizontal--sm*2};
		@include padding-horizontal($text-layout-space-horizontal--sm); } }

// максимальная ширина альтернативных элементов на внутренней (текстовой) странице
.max-wrap-flood {
	max-width: #{$flood-layout-width + $text-layout-space-horizontal*2};
	//max-width: #{$flood-layout-width + $text-layout-space-horizontal*2 + $text-layout-indent}

	@include media('<desk-lg') {
		max-width: #{$flood-layout-width + $text-layout-space-horizontal--lg*2}; }

	//+media('<tab-lg')
	//ax-width: #{$flood-layout-width + $text-layout-space-horizontal--md*2}

	@include media('<mob-lg') {
		max-width: #{$flood-layout-width + $text-layout-space-horizontal--sm*2}; } }

// выравнивание альтернативных элементов на внутренней (текстовой) странице
// и их максимальная ширина
.layout-flood {
	max-width: #{$flood-layout-width + $text-layout-space-horizontal*2};
	@include padding-horizontal($text-layout-space-horizontal);
	//max-width: #{$flood-layout-width + $text-layout-space-horizontal*2 + $text-layout-indent}
	//+padding-horizontal(#{$text-layout-indent + $text-layout-space-horizontal}, $text-layout-space-horizontal)
	@include push-auto;

	@include media('<desk-lg') {
		max-width: #{$flood-layout-width + $text-layout-space-horizontal--lg*2};
		@include padding-horizontal($text-layout-space-horizontal--lg); }

	//+media('<tab-lg')
	//ax-width: #{$flood-layout-width + $text-layout-space-horizontal--md*2}
	//include padding-horizontal($text-layout-space-horizontal--md)

	@include media('<mob-lg') {
		max-width: #{$flood-layout-width + $text-layout-space-horizontal--sm*2};
		@include padding-horizontal($text-layout-space-horizontal--sm); }

	p, ul, ol, h1, h2, h3, h4, h5, h6, hr, blockquote {
		max-width: none; } }

// элеметы с отступами по бокам
.layout-wide {
	@include padding-horizontal($text-layout-space-horizontal);

	@include media('<desk-lg') {
		@include padding-horizontal($text-layout-space-horizontal--lg); }

	//+media('<tab-lg')
	//include padding-horizontal($text-layout-space-horizontal--md)

	@include media('<mob-lg') {
		@include padding-horizontal($text-layout-space-horizontal--sm); }

	p, ul, ol, h1, h2, h3, h4, h5, h6, hr, blockquote {
		max-width: none; } }

// элеметы без отступов по бокам
.layout-full-width {

	p, ul, ol, h1, h2, h3, h4, h5, h6, hr, blockquote {
		max-width: none; } }

// выравнивание текстовых элементов на странице статьи (контент обернут в .layout-article)
// и их максимальная ширина
.layout-article {
	//padding: round($text-layout-space-horizontal) #{$text-layout-space-horizontal} round($text-layout-space-horizontal*1.5)

	//+media('<tab-lg')
	//adding: round($text-layout-space-horizontal--md*1.5) #{$text-layout-space-horizontal--md} round($text-layout-space-horizontal--md*2)

	//+media('<tab-sm')
	//adding: round($text-layout-space-horizontal--sm*1.5) #{$text-layout-space-horizontal--sm} round($text-layout-space-horizontal--sm*2)

	& > p, & > ul, & > ol, & > h1, & > h2, & > h3, & > h4, & > h5, & > h6, & > hr, & > blockquote, & > .layout-text {
		//max-width: $text-layout-width + $text-layout-indent
		//+padding-horizontal($text-layout-indent, 0)
		max-width: $text-layout-width;
		@include padding-horizontal(0);
		@include push-auto; }

	& > h1, & > img, .imgc {
		display: block;
		max-width: $flood-layout-width;
		@include push-auto; }

	& > img, .imgc {
		margin-bottom: $bottom-space--lg; }

	.layout-text, .layout-flood {
		@include padding-horizontal(0);
		@include push-auto; }

	.layout-text {
		max-width: $text-layout-width; }

	.max-wrap-text {
		max-width: $text-layout-width; }

	.layout-flood {
		//max-width: $flood-layout-width + $text-layout-indent
		//+padding-horizontal($text-layout-indent, 0)
		max-width: $flood-layout-width; }

	.max-wrap-flood {
		max-width: $flood-layout-width; }

	& > .pagination {
		max-width: $flood-layout-width;
		@include push-auto; }

	// элеметы без отступов по бокам
	// минусовые маржины по горизонтале имеют значение только внутри .layout-article
	// и если у .layout-article есть горизонтальные отступы
	//.layout-full-width, .layout-wide

	///+margin-horizontal(-#{$text-layout-space-horizontal})

	///+media('<desk-lg')
	//@include margin-horizontal(-#{$text-layout-space-horizontal--lg})

	///+media('<tab-lg')
	//@include margin-horizontal(-#{$text-layout-space-horizontal--md})

	///+media('<mob-lg')
 }	//@include margin-horizontal(-#{$text-layout-space-horizontal--sm})

.home-page {
	/* main slider (layout) */
	.main-slider-pagination {
		left: #{$layout-spaces};
		top: #{floor($layout-spaces*0.9)}; }

	.main-slider-prev, .main-slider-next {
		bottom: #{floor($layout-spaces*0.9)};
		top: auto;
		margin-top: 0; }

	.main-slider-prev {
		left: #{$layout-spaces}; }

	.main-slider-next {
		right: #{$layout-spaces}; }

	.main-slider__item {
		padding: #{floor($layout-spaces*0.9*2)} #{$layout-spaces};

		@include media('<desk-lg') {
			padding:  #{floor($layout-spaces--lg*0.9*2)} #{$layout-spaces--lg}; } }

	/* main slider (layout) end */

	/* tape slider (layout) */
	.tape-slider-pagination {
		bottom: #{floor($layout-spaces*0.9)}; }

	.tape-slider-prev, .tape-slider-next {
		bottom: #{floor($layout-spaces*0.9)};
		top: auto;
		margin-top: 0; }

	.tape-slider-prev {
		left: #{$layout-spaces}; }

	.tape-slider-next {
		right: #{$layout-spaces}; }

	/* tape slider (layout) end */

	/* previews tabs (layout) */
	.previews-tabs__nav {
		padding: #{floor($layout-spaces*0.9)} #{$layout-spaces}; }

	/* previews tabs (layout end) */ }

/* feature list, images slider */
.feature-list, .images-slider {
	max-width: $flood-layout-width;
	@include push-auto;

	&:last-child {
		margin-bottom: 0; } }

.feature-list {
	margin-bottom: $bottom-space--lg;

	@include media('<tab-lg') {
		margin-bottom: $bottom-space--ml; } }

.images-slider {
	margin-bottom: $bottom-space--lg*2;

	@include media('<tab-lg') {
		margin-bottom: $bottom-space--ml*2; } }

/* feature list, images slider end */

/* subscription form */
.subscription-form {
	padding: $layout-spaces;

	.article-footer + & {
		padding-top: 145px; } }

/* subscription form end */

/* bottom space */
.bottom-space {
	margin-bottom: $bottom-space--lg;

	@include media('<desk-lg') {
		margin-bottom: $bottom-space--ml; }

	&:last-child {
		margin-bottom: 0; } }

/* bottom space end */

/* footer at bottom */
.spacer {
	.wrapper > & {
		display: none;

