@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

/* slick sliders */
// arrows
$slick-arrow-icon-size: 6px;

.slick-arrow {
	@include size($slider-arrow-size);
	display: block;
	position: absolute;
	border: none;
	border-radius: 0;
	background: none;
	outline: none;
	box-shadow: none;
	opacity: 1;
	z-index: 6;
	@include margin-horizontal(-#{round($slick-arrow-icon-size*1.5/2)});

	@if $animation-use {
		transition: {
			property: background, opacity, visibility;
			duration: 0.2s; } }
	@else {
		transition: none; }

	&::before {
		content: none; }

	&::after {
		top: 50%;
		margin-top: -#{round($slick-arrow-icon-size/2)};
		content: ""; }

	.no-touchevents &:hover {
		opacity: 1;

		&.slick-prev {
			&::after {
				border-right-color: $base-color; } }

		&.slick-next {
			&::after {
				border-left-color: $base-color; } } }

	&.slick-disabled, .no-touchevents &.slick-disabled {
		opacity: 0.25;
		-ms-filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=25);
		filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=25);
		cursor: default;

		&::before {
			opacity: 1; } } }

.slick-prev {
	left: 0;

	&::after {
		@include triangle(left, $color: $text-color, $size: $slick-arrow-icon-size, $kef: 1.5);
		left: 50%; } }

.slick-next {
	right: 0;
	margin-right: {}

	&::after {
		@include triangle(right, $color: $text-color, $size: $slick-arrow-icon-size, $kef: 1.5);
		right: 50%; } }

// dots
.slick-dots {
	margin: 0;
	line-height: 0;

	.#{$user-content} & {
		margin: 0;
		max-width: none; }

	li {
		vertical-align: top;
		width: auto;
		height: auto;
		margin: 0;

		&, .#{$user-content} & {
			padding: 4px 10px;
			margin: 0;

			&::before {
				content: none; } }

		button {
			width: 12px;
			height: 12px;
			padding: 0;
			border-radius: 50%;
			background: $base-color;
			position: relative;

			@if $animation-use {
				transition: {
					property: background, border, box-shadow, transform;
					duration: 0.6s; } }
			@else {
				transition: none; }


			&::before {
				content: '';
				position: absolute;
				left: 2px;
				top: 2px;
				height: 8px;
				width: 8px;
				background: #fff;
				border-radius: 50%;
				opacity: 1;
				box-shadow: 0 2px 3px rgba(0, 0, 0, 0.20);

				@if $animation-use {
					transition: {
						property: transform, opacity;
						duration: $animation-speed; } }
				@else {
					transition: none; } } }

		&.slick-active button::before {
			opacity: 0; } } }

//.no-touchevents &:hover button::before
/* slick sliders end */
