@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

/* NAVIGATION */
.nav {
	position: relative; }

.nav-list {
	list-style: none;
	margin: 0 0 58px;
	padding: 0;

	&:last-child {
		margin-bottom: 0; }

	li {
		position: relative;
		margin-bottom: 12px;

		&:last-child {
			margin-bottom: 0; } }

	a {
		display: block;
		@include font-alt-medium;
		@include font(30px, round-float(30/30, 2));
		color: $text-color-base;
		border-color: $text-color-base; // для нижнего подчеркивания
		text-decoration: none;

		@if $animation-use {
			transition: {
				property: color, border;
				duration: $animation-speed; } }

		span {
			border-bottom: 2px solid;
			border-bottom-color: inherit; } }

	// hover
	a {
		&:hover {
			color: $base-color;
			border-color: $base-color; } }

	// current
	.current {
		a {
			color: $base-color;
			border-color: $base-color; } } }

/* NAVIGATION end */
