@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

/* SLIDERS */
/* images slider */
.images-slider {
	position: relative; }

.images-slider__list, .slick-dotted.images-slider__list {
	padding-bottom: 0;
	margin-bottom: 0;
	position: relative;
	z-index: 2; }

.images-slider__item {
	position: relative;
	overflow: hidden;
	float: none;
	@include display-inline-block-top;
	white-space: normal;
	background: #494949;
	opacity: 0;

	.slick-initialized & {
		display: block;
		float: left;
		opacity: 1; }

	.no-js & {
		display: block;
		width: 100%;
		opacity: 1; }

	&::before {
		content: '';
		display: block;
		height: 0;
		padding-top: 62.5%; }

	img {
		position: absolute;
		@include position(0);
		text-align: center;
		max-width: none;
		width: 100%;
		height: 100%;
		@include object-fit(contain); } }

.images-slider {

	.slick-arrow {
		right: 0;
		left: auto;
		top: 100%;
		transform: translate(0, 0);
		margin-top: -#{round($slider-arrow-size/2)};
		@include margin-horizontal(-110px); }

	.slick-prev {
		left: 50%; }

	.slick-next {
		right: 50%; }

	// slider counter
	.slider-counter {
		$sc-size: 256px, 86px;

		position: absolute;
		@include position(100%, 50%);
		padding: 31px 0 0;
		@include size(nth($sc-size, 1), nth($sc-size, 2));
		margin: -#{round(nth($sc-size, 2)/2)} 0 0 -#{round(nth($sc-size, 1)/2)};
		@include font-alt;
		@include font(24px, 1);
		color: $text-color;
		text-align: center;
		background-color: $bg-color-base;
		border: 1px solid $border-color;

		&::before, &::after {
			@include pseudo();
			@include position(50%);
			@include size(0, 30px);
			margin-top: -15px;
			border-left: 1px solid $border-color; }

		&::before {
			margin-left: -42px; }

		&::after {
			margin-left: 42px; }

		.slide-curr {
			color: $base-color;
			padding-right: 5px; }

		.slide-total {
			padding-left: 5px; } }

	.slick-dots, .#{$user-content} & .slick-dots {
		position: absolute;
		bottom: $bottom-space + 1;

		li {
			@include padding-horizontal(7px);

			button {
				width: 6px;
				height: 6px;
				border-radius: 50%;
				background-color: white;
				opacity: 0.5;

				@if $animation-use {
					transition: {
						property: opacity, transform;
						duration: $animation-speed; } }
				@else {
					transition: none; }

				&::before {
					content: none; } }

			&.slick-active button {
				opacity: 1; }

			.no-touchevents &:hover button {
				opacity: 1;
				transform: scale(2); } } } }

/* images slider end */
/* SLIDERS end */
