@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

/* HEADER */
// header layout
.header {
	background-color: rgba($bg-color, 0.3);
	box-shadow: inset 0 -1px 0 $border-color; }

.header > .layout {
	height: 100%; }

.header__holder {
	height: 100%;
	@include clear;
	text-align: center; }

.header__col {
	@include display-inline-block-top;
	text-align: left;
	height: 100%;
	white-space: nowrap;
	@include clear-spacing;

	&:first-child {
		display: block;
		float: left; }

	&:last-child {
		display: block;
		float: right; }

	&::before {
		@include pseudo-content();
		@include display-inline-block-middle;
		height: 100%; } }

.header__cell {
	@include display-inline-block-middle;
	@include font($font-size, $line-height);
	white-space: normal;
	letter-spacing: normal; }

// header layout end

/* slogan */
.slogan {
	text-decoration: none;
	color: #b2b2b2;
	@include font-alt-italic;
	@include font(18px, round-float(22/18, 2));
	padding-top: 0.5em;

	&:hover {
		color: $link-color; } }

/* slogan end */

/* menu */
.menu-container {
	&.header__col {
		height: 100%;

		.header__cell, .menu-list, .menu-list > li, .menu-list > li > a {
			height: 100%; } } }

.menu-list {
	$ml-space: 54px;

	& {
		list-style: none;
		padding: 0;
		margin: 0 -#{round($ml-space/2)};
		@include clear-spacing; }

	li {
		@include display-inline-block-middle;
		@include font-d; }

	a {
		display: table;
		width: 100%;
		text-decoration: none;
		color: $text-color;

		span {
			display: table-cell;
			vertical-align: middle;
			@include padding-horizontal(round($ml-space/2));
			border: solid transparent;
			border-width: 3px 0; } }

	// hover
	a {
		&:hover {
			color: $link-color; } }

	// current
	li {
		&.current {
			a {
				color: $link-color;

				span {
					border-bottom-color: $base-color; } } } } }

/* menu end */

/* HEADER end */
