@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

/* user form */
.form-caption {
	position: relative;
	display: block;
	margin-bottom: $bottom-space--md; }

.form-title {
	display: block;
	position: relative;
	width: 50%;
	@include font-alt-bold;
	@include font(30px, round-float(36/30, 2));
	color: $title-color;
	text-transform: uppercase;
	margin-bottom: $bottom-space--md;

	&::after {
		@include pseudo();
		@include position(100%);
		@include size(100px, 0);
		margin: -0.7em 0 0 20px;
		border-top: 1px solid $text-color; } }

.btn-reset {
	@include font($font-size, round-float(20/16, 2));
	border: none;
	border-bottom: 1px dashed $text-color;
	background: none;
	box-shadow: none;
	margin: 0;
	padding: 0;

	&:hover {
		color: $base-color;
		border-color: $base-color; }

	.form-caption & {
		position: absolute;
		right: 0;
		bottom: 0;
		margin-bottom: 0.6em; } }

.user-form {
	$label-width: 180px;

	& {
		background: $bg-color-alt;
		@include padding-vertical($bottom-space--lg);
		margin-bottom: $bottom-space--lg; }

	@include media('<tab-lg') {
		@include padding-vertical($text-layout-space-horizontal--md); }

	@include media('<mob-lg') {
		@include padding-vertical($text-layout-space-horizontal--sm); }

	&:last-child {
		margin-bottom: 0; }

	form {
		display: block; }

	.input-wrap {
		margin-bottom: 50px;
		@include clear; }

	.label-holder {
		float: left;
		width: $label-width;
		margin-bottom: 0;
		padding: 14px 20px 0 0; }

	.input-holder {
		margin-left: $label-width; }

	textarea {
		height: 130px;
		max-height: 130px;
		resize: none; }

	.captcha {
		@include display-inline-block-top;
		@include clear-spacing;
		position: relative; // Важно! Для позицирования отметки подтверждения полей формы
		z-index: 1; // Важно! Отметка подтверждения полей формы имеет z-index: -1

		@include clear;

		img {
			$margin-right: 20px;
			display: inline-block;
			vertical-align: middle;
			max-width: calc(50%);
			margin-right: $margin-right;

			& + input {
				display: inline-block;
				vertical-align: middle;
				width: 200px; } }

		@include media('<tab-lg') {
			img {
				display: block;
				max-width: none;
				margin-bottom: $bottom-space--sm;

				& + input {
					display: block;
					max-width: none;
					width: 100%; } } } }

	.form-footer {
		padding-left: $label-width;
		@include clear;

		@include media('<mob-lg') {
			padding-top: $bottom-space--sm; } }

	.form-buttons {
		float: right;
		margin-left: 30px;

		@include media('<mob-lg') {
			float: none;
			margin: 0; }

		.btn-submit {
			width: 200px; } }

	.form-note {
		overflow: hidden;
		min-height: $form-input-height;
		@include clear-spacing;
		line-height: 25px;

		&::before {
			content: '';
			display: inline-block;
			vertical-align: middle;
			min-height: inherit; }

		.form-mark {
			margin-left: 0;
			left: auto; } }

	.form-note__item {
		display: inline-block;
		vertical-align: middle;
		width: calc(100% - 6px);
		@include font($font-size, round-float(20/16, 2));
		letter-spacing: normal; } }

/* user form end */
