@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

/* BUTTON */
[class|="btn"], [class*=" btn-"], input[type="submit"], input[type="button"], input[type="reset"], button {
	display: inline-block;
	vertical-align: top;
	position: relative;
	text-align: center;
	text-decoration: none;
	cursor: pointer; }

[class|="btn"], [class*=" btn-"] {
	& input[type="submit"], & input[type="button"], & input[type="reset"] {
		background: url("../../img/empty.png") repeat;
		display: block;
		@include cover-abs();
		padding: 0;
		border: none;
		z-index: 99;
		opacity: 0; } }

/* btn-default */
[class*="btn-default"] {
	position: relative;
	@include font-bold;
	@include font(14px, 18px);
	letter-spacing: 0.2em;
	text-transform: uppercase;
	color: $btn-text-color;
	fill: $btn-text-color;
	padding: 14px 38px;
	border: 2px solid transparent;
	background: $btn-bg-color;
	box-shadow: none;
	margin: 0;

	@if $animation-use {
		transition: {
			property: background-color, color, border-color;
			duration: $animation-speed; } }

	// hover
	.no-touchevents &:hover {
		background-color: $btn-bg-hover-color; }

	// disabled
	&:disabled, &.disabled, &[data-disabled] {
		cursor: default;
		background-color: $btn-disabled-color !important;

		&::before, &::after {
			display: none; } } }

/* btn-default end */

/* btn-default-alt */
.btn-default-alt {
	color: $btn-bg-color;
	border: 2px solid $btn-bg-color;
	background: $btn-text-color;

	.no-touchevents &:hover {
		background: $btn-bg-color;
		color: $btn-text-color; }

	&:disabled, &.disabled, &[data-disabled] {
		cursor: default;
		background-color: white !important;
		border-color: $btn-disabled-color !important;
		color: $btn-disabled-color !important;

		&::before, &::after {
			display: none; }

		& > i {
			&::before, &::after {
				border-color: $btn-disabled-color !important;
				fill: $btn-disabled-color !important; } } } }

/* btn-default-alt end */

/* btn-with-icon */
.btn-with-icon {
	& > i, & > span {
		@include display-inline-block-top; }

	& > i {
		margin-right: 10px;
		line-height: 1; }

	svg {
		@include display-inline-block-top;
		@include size(24px); }

	path {
		fill: inherit;

		@if $animation-use {
			transition: {
				property: fill;
				duration: $animation-speed; } } }

	& > span {
		text-align: left;
		white-space: normal; }

	.depict-send {
		svg {
			@include size(40px, 16px);
			margin-top: 4px;
			margin-bottom: -4px; }

		& + span {
			max-width: calc(100% - 5px - 40px - 10px); } } }

/* btn-with-icon end */

/* btn-with-arrow */
.btn-with-arrow {
	$arrow-width: 26px;
	$arrow-space: 10px;

	& > i, & > span {
		@include display-inline-block-middle; }

	& > i {
		position: relative;
		margin-right: $arrow-space;
		@include font(0, 0);
		@include size($arrow-width, 18px);

		&::before, &::after {
			@include pseudo();
			@include position(50%);
			border: 0 solid $btn-text-color;
			transform: none;

			@if $animation-use {
				transition: {
					property: border-color;
					duration: $animation-speed; } } }

		&::before {
			@include size(26px, 2px);
			border-top-width: 2px;
			margin: -1px 0 0 round(-$arrow-width/2); }

		&::after {
			top: 0;
			@include size(12px);
			border-width: 2px 0 0 2px;
			transform: rotate(-45deg);
			margin: 3px 0 0 round(-$arrow-width/2); } }

	& > span {
		text-align: left;
		white-space: normal;
		max-width: calc(100% - #{$arrow-width} - #{$arrow-space});

		& ~ i {
			margin-right: 0;
			margin-left: $arrow-space;

			&::after {
				border-width: 0 2px 2px 0;
				margin: 3px 0 0 1px; } } }

	// alt button
	&.btn-default-alt {

		& > i {
			&::before, &::after {
				border-color: $btn-bg-color; } } }

	// hover
	.no-touchevents &.btn-default-alt:hover {

		& > i {

			&::before, &::after {
				border-color: $btn-text-color; } } } }

/* btn-with-arrow end */

/* button toggle menu */
.btn-menu {
	$btn-menu-bg: $bg-color;
	$btn-menu-color: $text-color;
	$btn-menu-lines-width: 26px;
	$btn-menu-lines-height: 3px;
	$btn-menu-lines-space: 5px;
	$btn-menu-lines-color: $text-color;
	$btn-menu-animation-speed: $animation-speed;
	$btn-menu-animation-delay: round-float($animation-speed*0.5, 2);

	$user-options-cross-width: $btn-menu-lines-width;
	$user-options-cross-height: $btn-menu-lines-height;
	$user-options-cross-color: $btn-menu-lines-color;

	@mixin menu-btn-line() {
		@include size($btn-menu-lines-width, $btn-menu-lines-height);
		background-color: $btn-menu-lines-color; }

	@mixin menu-btn-cross-line() {
		@include size($user-options-cross-width, $user-options-cross-height);
		background-color: $user-options-cross-color; }

	display: block;
	vertical-align: top;
	position: relative;
	text-decoration: none;
	color: $text-color;
	@include size($sidebar-width);
	background-color: $bg-color-base;

	@if $animation-use {
		transition: {
			property: background-color;
			duration: $btn-menu-animation-speed; } }

	@include media('<tab-lg') {
		@include size($sidebar-width--lg); }

	& > span {
		@include hide-text;
		white-space: normal; }

	// крест
	&::before, &::after {
		@include pseudo();
		@include menu-btn-cross-line;
		@include position(50%);
		@include margin($top: -#{round($user-options-cross-height*0.5)}, $left: -#{round($user-options-cross-width*0.5)});
		will-change: opacity, transform;
		opacity: 0;
		transition: {
			property: transform, opacity;
			duration: $btn-menu-animation-speed;
			delay: 0s;
			timing-function: cubic-bezier(0.65, 0.05, 0.36, 1); } }

	// бутерброд
	& > i {
		position: absolute;
		left: 50%;
		top: 50%;
		@include menu-btn-line;
		@include margin($top: -#{round($btn-menu-lines-height*0.5)}, $left: -#{round($btn-menu-lines-width*0.5)});
		backface-visibility: hidden;
		transition: {
			property: opacity;
			duration: $btn-menu-animation-speed;
			delay: $btn-menu-animation-delay; }

		@if $animation-use {
			transition: {
				property: background-color, opacity;
				duration: $btn-menu-animation-speed; } }

		&::before, &::after {
			@include pseudo();
			left: 0;
			@include menu-btn-line;
			backface-visibility: hidden;
			transition: {
				property: transform;
				duration: $btn-menu-animation-speed;
				delay: $btn-menu-animation-delay; }

			@if $animation-use {
				transition: {
					property: transform, opacity, background-color;
					duration: $btn-menu-animation-speed; } } }

		//first line menu
		&::before {
			top: -#{$btn-menu-lines-height + $btn-menu-lines-space}; }

		//third line menu
		&::after {
			top: #{$btn-menu-lines-height + $btn-menu-lines-space}; } }

	// hover
	.no-touchevents &:hover {

		&::before, &::after {
			background-color: $base-color; }

		& > i {
			background-color: $base-color;

			&::before, &::after {
				background-color: $base-color; } } }

	// active
	&.active {

		&::before, &::after {
			transition-delay: $btn-menu-animation-delay;

			opacity: 1; }

		&::before {
			transform: rotate(45deg) translateZ(0); }

		&::after {
			transform: rotate(-45deg) translateZ(0); }

		& > i {
			opacity: 0;
			transition-delay: 0s;

			&::before, &::after {
				transition-delay: 0s; }

			&::before {
				transform: translate(0, #{$btn-menu-lines-height + $btn-menu-lines-space}) translateZ(0); }

			&::after {
				transform: translate(0, -#{$btn-menu-lines-height + $btn-menu-lines-space}) translateZ(0); } } }

	// hover on active
	//.no-touchevents &.active:hover

	// > i

	//&::before, &::after
 }	//	background-color: $bg-color-base

/* button toggle menu end */

/* button popup close */
.btn-popup-close {
	//<a href="#" class="btn-popup-close js-btn-menu-close"><i></i><span>Закрыть</span></a>
	$btn-bg: #f3f3f3;
	$btn-color: $text-color;
	$btn-size: 60px;
	$btn-space: 12px;
	$btn-lines-width: 20px;
	$btn-lines-height: 3px;
	$btn-lines-space: 5px;
	$btn-lines-color: $text-color;

	@mixin line-style() {
		@include size($btn-lines-width, $btn-lines-height);
		border-radius: 2px; }

	display: block;
	@include size($btn-size);
	background: $btn-bg;
	color: white;
	text-decoration: none;
	@include clear-spacing;
	white-space: nowrap;
	transition-property: background-color, opacity, visibility;
	transition-duration: $animation-speed;

	@include media('<mob-lg') {
		@include size(45px); }

	&::before {
		content: '';
		display: inline-block;
		height: $btn-size;
		min-height: 100%;
		vertical-align: middle; }

	> span {
		@include hide-text; }

	> i {
		display: inline-block;
		vertical-align: middle;
		position: relative;
		backface-visibility: hidden;
		background-color: transparent;
		@include line-style;

		&::before, &::after {
			@include pseudo();
			left: 0;
			backface-visibility: hidden;
			@include line-style;
			background: $btn-lines-color;
			transition-property: background-color;
			transition-duration: $animation-speed; }

		//first line menu
		&::before {
			top: 0;
			transform: rotate(-45deg); }

		//third line menu
		&::after {
			top: 0;
			transform: rotate(45deg); } }

	.no-touchevents &:hover {
		background-color: $mark-color; } }

/* button popup close end */

/* arrow left */
.arr-left, .arr-right {
	position: relative;
	@include display-inline-block-top;
	@include size(50px);
	text-decoration: none;
	color: $text-color;

	& > span {
		@include hide-text; }

	& > i {
		position: absolute;
		@include position(50%);
		@include clear-spacing;
		transform: translate(-50%, -50%); } }

.arr-left {
	& > i {
		@include triangle(left, $text-color, 6px); } }

.arr-right {
	& > i {
		@include triangle(right, $text-color, 6px); } }

