@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

/* SIDEBAR */
.sidebar {
	background-color: rgba($bg-color, 0.3);
	border: solid $border-color;
	border-width: 0 1px 0 0; }

.sidebar__ceil {
	position: relative;

	.v-middle--top & {
		padding-top: $sidebar-inside-space; }

	.v-middle--bottom & {
		padding-bottom: $sidebar-inside-space; }

	&::after {
		display: none;
		@include pseudo-content();
		position: absolute;
		left: 50%;
		@include size(0, 100px);
		border-left: 1px solid $border-color;

		.v-middle--top & {
			top: 100%;
			display: block;
			margin-top: 25px; }

		.v-middle--bottom & {
			bottom: 100%;
			display: block;
			margin-bottom: 25px; } } }

/* SIDEBAR end */
