@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

html {
	font-size: 62.5%;

	// блокировка скролла
	&.css-scroll-fixed {
		@include scroll-blocked(); } }

body {
	@include font-normal;
	@include font($font-size, $line-height);
	letter-spacing: $letter-spacing;
	color: $text-color;
	background: $bg-color url(../../img/empty.png) no-repeat left top fixed;

	// выделение текста
	*::selection {
		background-color: $base-color;
		color: white; } }

h1, h2, h3, h4, h5, h6 {
	padding: 0;
	@include font-alt-bold;
	color: $title-color;
	margin: 0 0 $bottom-space--md;

	@include media('<mob-lg') {
		margin-bottom: round-float($bottom-space*0.85, 1); }

	&:last-child {
		margin-bottom: 0; }

	a {
		text-decoration: none;
		color: inherit;

		.no-touchevents &:hover {
			color: $base-color;
			text-decoration: underline; } } }

h1 {
	margin-bottom: $bottom-space--lg;
	@include font($font-size-h1, $line-height-h1);
	letter-spacing: 0.05em;
	text-align: center;

	@include media('<desk-hd') {
		@include font($font-size-h1--lg, $line-height-h1--lg); }

	@include media('<desk-lg') {
		margin-bottom: $bottom-space--ml; }

	@include media('<desk-md') {
		@include font($font-size-h1--md, $line-height-h1--md); }

	@include media('<mob-lg') {
		@include font($font-size-h1--sm, $line-height-h1--sm); } }

h2 {
	@include font($font-size-h2, $line-height-h2);

	@include media('<desk-md') {
		@include font($font-size-h2--md, $line-height-h2--md); }

	@include media('<mob-lg') {
		@include font($font-size-h2--sm, $line-height-h2--sm); } }

h3 {
	@include font($font-size-h3, $line-height-h3);

	@include media('<desk-md') {
		@include font($font-size-h3--md, $line-height-h3--md); } }

h4 {
	@include font($font-size-h4, $line-height-h4); }

h5 {
	@include font($font-size-h5, $line-height-h5); }

h6 {
	@include font($font-size-h6, $line-height-h6);
	text-transform: uppercase; }

p {
	padding: 0;
	margin: 0 0 $bottom-space;

	&:last-child {
		margin-bottom: 0; } }

hr {
	line-height: 1px;
	height: 0;
	border: none;
	background: none;
	border-bottom: 1px solid $border-color;
	margin-bottom: $bottom-space--lg; }

a {
	color: $link-color;
	text-decoration: underline;
	cursor: default;
	outline: none;

	&:link {
		cursor: pointer; }

	.no-touchevents &:hover {
		text-decoration: none; }

	&[href^="tel"], .no-touchevents &[href^="tel"]:hover {
		text-decoration: none !important;
		color: inherit;
		cursor: default; }

	&[href^="javascript:"] {
		cursor: default; } }

img {
	border: none;
	vertical-align: top;
	max-width: 100%;

	table & {
		max-width: none; } }

b, strong, th {
	@include font-bold; }

i, em {
	@include font-italic; }

i b, i strong, em b, em strong, b i, b em, strong i, strong em {
	@include font-bold-italic; }

.font-d {
	@include display-inline-block-top;
	@include font-d;
	text-decoration: none;
	color: $text-color;

	& > span {
		@include display-inline-block-top;
		border-bottom: 2px solid $text-color;
		line-height: 28px; }

	// hover
	&:hover {
		color: $base-color;

		& > span {
			border-color: $base-color; } } }

.entry-text {
	position: relative;
	margin-bottom: $bottom-space--lg;
	padding-bottom: $bottom-space--md;
	@include font-alt-italic;
	@include font(30px, round-float(36/30, 2));
	text-align: center;

	&::after {
		$width: 100px;

		@include pseudo();
		@include position(100%, 50%);
		@include size($width, 0);
		margin-left: -#{round($width/2)};
		border-top: 1px solid $text-color; }

	&:last-child {
		margin-bottom: 0; }

	b, strong,
	i, em {
		@include font-alt-bold; } }

.entry-img {
	position: relative;
	overflow: hidden;
	margin-bottom: $bottom-space--lg;
	height: calc(100vh - #{$header-height});
	min-height: 800px;

	&::before {
		@include pseudo();
		@include position(0, 0);
		@include size(100%);
		background-color: rgba($text-color, 0.35);
		z-index: 2; }

	@include media('<mob-md') {
		@include ratio(16, 9); }

	img {
		position: absolute;
		left: 50%;
		top: 50%;
		max-width: 100%;
		max-height: none;
		min-width: 0;
		min-height: 0;
		text-align: left;
		width: calc(100% + 4px);
		height: calc(100% + 4px);
		transform: translate(-50%, -50%);
		@include object-fit(cover);
		z-index: 1;

		//.no-objectfit &
		//idth: auto
 }		//eight: 100%

	.breadcrumbs {
		position: relative;
		z-index: 2;
		margin-bottom: 0;

		li, a {
			color: white; } }

	.datetime {
		display: block;
		@include font-d;
		color: white;
		text-align: center;
		margin-bottom: 3em; } }

.entry-img__content {
	position: absolute;
	@include position(50%);
	width: 100%;
	max-width: $flood-layout-width;

	@include padding-horizontal(0 !important);
	transform: translate(-50%, -50%);
	z-index: 3; }


.entry-img__title {
	margin: 0;
	@include font($font-size-h1, $line-height-h1);
	color: $bg-color-base;
	text-shadow: 0 5px 20px rgba(0, 0, 0, 0.2);
	text-align: center;

	@include media('<desk-md') {
		@include font($font-size-h1--md, $line-height-h1--md); }

	@include media('<mob-lg') {
		@include font($font-size-h1--sm, $line-height-h1--sm); }

	@include media('<mob-md') {
		@include font(22px, 1); } }

.mark {
	color: $mark-color;
	font-style: inherit;
	font-weight: inherit; }

.text-color-alt {
	color: $base-color; }

.align-center {
	text-align: center; }

.align-left {
	text-align: left; }

.align-right {
	text-align: right; }

.float-left {
	float: left; }

.float-right {
	float: right; }

.nobr {
	white-space: nowrap; }

.hide-text {
	@include hide-text; }

/* content img */
.imgc {
	text-align: center;
	margin-bottom: $bottom-space;
	clear: both; }

.imgl {
	text-align: left;
	float: left;
	margin: 0 $bottom-space $bottom-space 0 !important; }

.imgr {
	text-align: right;
	float: right;
	margin: 0 0 $bottom-space $bottom-space !important; }

.imgl, .imgr {
	max-width: 40% !important;

	@include media('<tab-sm') {
		float: none;
		display: block;
		margin-left: auto;
		margin-right: auto;
		max-width: 100% !important; } }

/* content img end */

/* columnize */
.columnize, .columnize--3, .columnize--4 {
	column-count: 2;
	column-gap: 2em;
	margin-bottom: $bottom-space;
	font-size: 0.9em; }

.columnize--3 {
	column-count: 3;
	font-size: 0.85em; }

.columnize--4 {
	column-count: 4;
	font-size: 0.8em;

	@include media('<desk-sm') {
		column-count: 3; } }

.columnize--3, .columnize--4 {

	@include media('<tab-lg') {
		column-count: 2; } }

.columnize, .columnize--3, .columnize--4 {

	@include media('<tab-sm') {
		column-count: 1; } }

/* columnize end */

.#{$user-content} {
	@each $tags in h2, h3, h4, h5 {
		p + #{$tags},
		ul + #{$tags},
		ol + #{$tags} {
			margin-top: round-float($bottom-space--lg, 1); } } }

/* list */
.#{$user-content} {
	//list
	ul, ol {
		margin: 0 0 $bottom-space;
		padding: 0;
		list-style: none;

		li {
			padding-left: 30px;
			margin: 0 0 round($bottom-space*0.5);
			position: relative; } }

	ul > li::before {
		@include pseudo();
		left: 0;
		top: round-float($font-size*$line-height/2, 1);
		@include size(0);
		border: 4px solid $base-color;
		margin-top: -4px;
		border-radius: 50%; }

	ol {
		counter-reset: section;

		& > li {
			counter-increment: section;
			height: 1%;
			overflow: hidden;

			&::before {
				position: relative;
				display: inline-block;
				text-align: left;
				@include font-bold;
				color: $base-color;
				content: counter(section);
				padding: 0 4px 0 0;
				min-width: 30px;
				margin-left: -30px; } } }

	ul, ol {
		ul, ol {
			margin: round($bottom-space*0.5) 0; } } }

/* list reset */
.reset-list,
.#{$user-content} .reset-list {
	list-style: none;

	li {
		&, &:first-child {
			padding: 0;

			&::before {
				content: none; } } } }

/* list reset end */
/* list end */

/* table */
table {
	border-collapse: collapse;
	border-spacing: 0;
	width: auto;

	td, th {
		@include font-normal;
		@include font($font-size, round-float(20/16,2));
		color: inherit;
		padding: 20px 30px;
		border-color: $border-color; }

	th, thead td {
		@include font-bold; } }

.table-auto {
	position: relative;
	overflow: auto;
	max-width: 100%;
	-webkit-overflow-scrolling: touch;
	margin-bottom: $bottom-space--lg;

	table {
		margin-bottom: 0;
		width: auto;
		min-width: 100%; } }

.#{$user-content} {
	table {
		margin-bottom: $bottom-space--lg; }

	.table-auto table {
		margin-bottom: 0; }

	.tbl {
		border: none;

		td, th {
			color: $text-color;
			background-color: $bg-color;
			border: 0 solid $border-color;

			&:first-child, &:first-child {
				border-left-width: 0; }

			&:last-child, &:last-child {
				border-right-width: 0; } }

		tr:hover {
			td, th {
				background-color: $bg-color-alt; } }

		//tr:last-child
		//d
		//border-bottom-width: 1px

		td {
			border-bottom-width: 1px; }

		tbody tr:first-child td {
			border-top-width: 1px; }

		//tbody tr:nth-child(even) td
		//ackground: $base-color
		//olor: $bg-color-base

		th, thead td {
			@include font-bold;
			font-size: $font-size--sm;
			letter-spacing: 0.025em;
			text-align: left;
			text-transform: uppercase; }

		th, thead td, tfoot td {
			background: $bg-color-alt;
			border-top-width: 0;
			border-bottom-width: 0; } } }

/* table end */

/* blockquote */
.#{$user-content} {
	$bq-width: 540px;

	blockquote {
		display: block;
		margin: $bottom-space--lg 0;
		padding: 0;

		q {
			margin: 0;
			padding: 0 0 $bottom-space;
			@include font-alt-italic;
			@include font(3rem, round-float(36/30, 2));
			color: $text-color;
			text-align: center;

			&::before, &::after {
				@include pseudo-block-relative();
				width: 24px;
				height: 15px;
				@include push-auto;
				background-position: center;
				background-repeat: no-repeat;
				background-size: contain;
				line-height: 1; }

			&::before {
				content: '';
				margin-bottom: $bottom-space;
				background-image: url(../../img/ql.png); }

			&::after {
				content: '';
				margin-top: $bottom-space;
				background-image: url(../../img/ql.png); } }

		cite {
			@include font(2rem, round-float(36/30, 2));
			color: $base-color;
			text-align: right; }

		q, cite {
			display: block;
			max-width: $bq-width;
			@include push-auto;
			@include font-alt-italic;
			@include padding-horizontal(#{$text-layout-space-horizontal}); } } }

/* blockquote end */

/* user spacer */
.user-spacer {
	display: block;
	height: $bottom-space--lg;
	@include font(0, 0);
	@include clear;

	@include media('<desk-lg') {
		height: $bottom-space--ml; } }

/* user spacer end */

/* datetime */
.datetime {
	display: block;
	font-style: normal;
	margin-bottom: $bottom-space; }

/* datetime end */

/* view print */
.view-print {
	@include display-inline-block-top;
	margin-bottom: $bottom-space--lg;
	line-height: 1;
	color: $text-color-base;
	fill: $text-color-base;
	text-decoration: none;

	@if $animation-use {
		transition: {
			property: background-color;
			duration: $animation-speed; } }

	@include media('<desk-lg') {
		margin-bottom: $bottom-space--ml; }

	@include media('<tab-lg') {
		display: none; }

	&:last-child {
		margin-bottom: 0; }

	svg {
		display: inline-block;
		vertical-align: middle;
		width: 20px;
		height: 20px;
		margin-right: 5px; }

	path {
		fill: inherit;

		@if $animation-use {
			transition: {
				property: fill;
				duration: $animation-speed; } } }

	span {
		display: inline-block;
		vertical-align: middle; }

	// hover
	.no-touchevents &:hover {
		color: $base-color;
		fill: $base-color; } }

/* view print end */
