@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

// pagination
$pagination-hspace: round($grid-space/2);
$pagination-vspace: 4px;
$pagination-font-size: 14px;
$pagination-height: 30px;
$pagination-width: $pagination-height;
$pagination-border-width: 0;

/* pagination */
.pagination {
	@include font-bold;
	@include clear-spacing;
	color: inherit;
	fill: inherit;
	text-align: center;
	cursor: default;
	margin-bottom: $bottom-space--lg;

	@include media('<desk-lg') {
		margin-bottom: $bottom-space--ml; }

	&:last-child {
		margin-bottom: 0; } }

.pg-left {
	float: left; }

.pg-right {
	float: right; }

.pg-first-arr, .pg-last-arr, .pg-prev-arr, .pg-next-arr {
	display: none;
	position: absolute;
	@include position(50%, 0);
	@include size(100%, 2px);
	margin-top: -1px;
	letter-spacing: 0;
	border-top: 2px solid $text-color;

	@if $animation-use {
		transition: {
			property: border-color;
			duration: $animation-speed; } }

	&::before, &::after {
		@include pseudo();
		top: 0;
		@include size(14px);
		margin-top: -1px;
		border: 0 solid $text-color;
		backface-visibility: hidden;

		@if $animation-use {
			transition: {
				property: border-color;
				duration: $animation-speed; } } } }

.pg-first-arr, .pg-prev-arr {
	&::before, &::after {
		transform: rotate(-45deg);
		transform-origin: left top;
		left: 0;
		margin-left: -1px;
		border-width: 2px 0 0 2px; } }

.pg-last-arr, .pg-next-arr {
	&::before, &::after {
		right: 0;
		margin-right: -1px;
		transform-origin: top right;
		transform: rotate(45deg);
		border-width: 2px 2px 0 0; } }

.pg-first-arr {

	&::after {
		margin-left: 6px; } }

.pg-last-arr {

	&::after {
		margin-right: 6px; } }

.pg-prev-arr, .pg-next-arr {

	&::after {
		display: none; } }


.pg-first, .pg-last, .pg-prev, .pg-next {
	@include display-inline-block-middle;
	position: relative;
	//width: 27px
	@if $pagination-border-width != 0 {
		margin: 0 0 0 -#{$pagination-border-width}; }
	@else {
		margin: #{$pagination-vspace} #{$pagination-hspace}; }
	color: inherit;
	text-decoration: none;

	&.disabled {
		opacity: 0.5;
		cursor: default; }

	.no-touchevents &:not(.disabled):hover {
		color: $base-color;

		[class*="-arr"] {
			&, &::before, &::after {
				border-color: $base-color; } } }

	&:not(.disabled):active {
		margin-top: 1px;
		margin-bottom: -1px; } }

.pg-first, .pg-last {
	display: none;

	@include media('<mob-lg') {
		display: none; } }

.pg-list {
	@include display-inline-block-middle;

	&, .#{$user-content} & {
		margin: 0;

		li {
			margin: #{$pagination-vspace} 4px;
			padding: 0;

			@include media('<mob-md') {
				@include margin-horizontal(0); }

			&:before {
				content: none; } } }

	li {
		@include display-inline-block-top;
		text-align: center;

		a, & > span {
			@include display-inline-block-top;
			color: inherit;
			background: transparent;
			min-width: $pagination-width;
			padding: 0 2px;
			border-radius: 50%;

			@if $animation-use {
				transition: {
					property: color;
					duration: $animation-speed; } } }

		a {
			text-decoration: none;
			cursor: pointer;

			.no-touchevents &:hover {
				color: $base-color; } }

		&.active a, .no-touchevents &.active a:hover {
			color: white;
			background-color: $base-color;
			@if $pagination-border-width != 0 {
				border: {
					top-color: $base-color;
					bottom-color: $base-color; } } } } }

// шрифт для элементов пагинации
.pg-first, .pg-last, .pg-prev, .pg-next, .pg-list a, .pg-list li > span {
	height: $pagination-height;
	@include font($pagination-font-size, #{$pagination-height - $pagination-border-width*2});
	letter-spacing: 0.2em;
	text-transform: uppercase;
	@if $pagination-border-width != 0 {
		border: #{$pagination-border-width} solid #{$border-color}; }

	@include media('<mob-md') {
		font-size: 14px; } }

.pg-list a, .pg-list li > span {
	letter-spacing: normal; }

/* pagination end */
